* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin: 0;
}

a, a:visited {
  color: #03a9f4;
}

header {
  position: fixed;
  padding: 10px 20px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
}

header a {
  margin-right: 10px;
  text-decoration: none;
}

header h1 {
  font-size: 1rem;
  margin: 0 0 0 5px;
}

header .logo {
  display: flex;
}

header .right {
  display: flex;
  align-items: center;

}

main {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
}

main .logo {
  display: flex;
  justify-content: center;
  margin: 80px auto 0;
  font-size: 32px;
}

main .logo h1 {
  margin: 0 10px;
  font-size: 1.2em;
}

h1.tagline {
  font-size: 36px;
  text-align: center;
  margin: 40px 0 40px;
}

.twitter {
  display: inline-block;
  width: 16px;
  height: 16px;
}

.call-to-action {
  margin: 10px 0 60px;
  text-align: center;
}

.call-to-action button {
  cursor: pointer;
  padding: 10px 30px;
  font-size: 24px;
  border-radius: 5px;
  border: 1px solid #eee;
  color: #fff;
  background-color: #03a9f4;
}

img.screenshot {
  width: 80%;
  margin: 0 auto;
}

section {
  width: 80%;
  margin: 0 auto;
  padding: 0 10px;
}

.features {
  margin-top: 100px;
}

.feature {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.feature img {
  width: 800px;
  max-width: 95%;
  border: 1px solid #eee;
}

.feature h2 {
  flex: 1 1 200px;
}

.subscription {
  margin: 70px 0 70px;
}

.pricing {
  text-align: center;
}

.pricing ul {
  padding: 0;
  list-style: none;
}

.pricing-cards {
  display: flex;
  justify-content: center;
  gap: 40px;
}

.pricing-card {
  width: 250px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.coming-soon {
  background-color: #03a9f4;
  color: #ffffff;
  padding: 2px 10px;
  border-radius: 3px;
}

@media (max-width: 1200px) {
  .feature {
    flex-direction: column;
  }
}

@media (max-width: 800px) {
  section {
    width: 100%;
  }
}
